* {
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease; }

*::-moz-selection {
  background: rgba(255, 255, 255, 0.2);
  color: #4CAF50; }

*::selection {
  background: rgba(255, 255, 255, 0.2);
  color: #4CAF50; }

.container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 670px;
  height: 125px; }
  @media (max-width: 670px) {
    .container {
      width: 100%;
      height: 200px; } }

.icon {
  margin: 0;
  padding: 0;
  line-height: 112.5px;
  font-size: 75px;
  display: inline-block;
  color: #4CAF50;
  opacity: 0.30;
  float: left;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default; }
  @media (max-width: 670px) {
    .icon {
      width: 100%;
      text-align: center; } }
  .icon .brace {
    padding: 0 11.25px;
    color: #fcfcfc;
    font-size: 112.5px;
    opacity: 0.20;
    font-weight: bold;
    display: inline-block; }

.text {
  margin-top: 40px;
  display: inline-block;
  color: white;
  float: left; }
  @media (max-width: 670px) {
    .text {
      margin-top: 0;
      width: 100%;
      text-align: center; } }
  .text h1 {
    letter-spacing: 5px;
    margin: 0;
    font-size: 45px; }
    @media (max-width: 670px) {
      .text h1 {
        font-size: 35px; } }

.personal-links {
  font-family: 'Source Code Pro', monospace;
  margin-top: 10px;
  line-height: 11px;
  letter-spacing: 0.5px; }
  .personal-links a {
    margin-right: 10px; }

a {
  position: relative;
  font-size: 11px;
  line-height: 15px;
  color: white;
  text-decoration: none; }
  a::before {
    -webkit-transform: scaleX(0);
        -ms-transform: scaleX(0);
            transform: scaleX(0);
    content: "";
    width: 100%;
    position: absolute;
    bottom: -5px;
    left: 0;
    border-bottom: solid 1px;
    border-color: #4CAF50;
    -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    -o-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
    transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; }
  a:hover::before {
    -webkit-transform: scaleX(1);
        -ms-transform: scaleX(1);
            transform: scaleX(1); }

.labs-link {
  position: absolute;
  top: 15px;
  right: 15px; }
  .labs-link a {
    color: white;
    text-decoration: none;
    opacity: 0.3; }
    .labs-link a:hover {
      opacity: 0.75; }



